body { 
  font-family: 'Noto Serif', serif;
  font-size: 20px;

 }

.start {
  justify-content: center;
  align-items: center;
  display: flex;
}

.detail {
  justify-content: center;
  align-items: center;
  display: flex;
}

.App {
  min-height: 60vh;
  background-image: url("img/chad-montano-MqT0asuoIcU-unsplash.jpg");;
}

.search-form {
  min-height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.search-bar {
  width: 50%;
  border: none;
  padding: 10px;
}

.search-button {
  background: #228B22;
  border: none;
  padding: 10px 20px;
  color: white;
}

.search-button:hover {
  background: rgb(98, 88, 88);
}

.recipes {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

